.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.climbingSection:hover, .wall-route:hover {
  cursor: pointer;
}

.right {
  text-align: right;
}
.image-container {
  position: relative;
}

.image-title {
  position: absolute;
    top: 5%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  color: white;
}

.image-title2 {
  position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;
    text-align: center;
}
.brand-name {
  font-size: 25px;
}

.select-dropdown {
  max-height: 200px;
}

.preloader-wrapper {
  display: block;
  margin: 50px auto;
}

nav i.material-icons {
  font-size: 36px;
}

.carabiners-container {
  width: 75%;
}

[type="radio"]:not(:checked) + label::before, [type="radio"]:not(:checked) + label::after, [type="radio"]:checked + label::after, .with-gap[type="radio"]:checked + label::after {
    display: none;
}

[type="radio"]:not(:checked)+label, [type="radio"]:checked+label {
padding: none;
margin: none;
cursor: pointer;
display: inline-block;
height: 40px;
line-height: 0;
font-size: 0;
-webkit-transition: .28s ease;
-o-transition: .28s ease;
transition: .28s ease;
  float: right;
}

[type="radio"]:hover ~label img {
     -webkit-filter: opacity(50%);
             filter: opacity(50%);
 }

[type="radio"]:checked ~label img {
      -webkit-filter: opacity(90%);
              filter: opacity(90%);
 }

label img {
    -webkit-filter: opacity(20%);
            filter: opacity(20%);
}

.carabiners img {
  height: 30px;
  width: auto;
}
img.unfilled {
    -webkit-filter: opacity(20%);
            filter: opacity(20%);
}

@media only screen and (max-width: 600px){
  nav > .container{
    width:100%;
  }
}



